.container > .product-details {
	margin-top: @grid-gutter-width;
}

.image-gallery {
	.gallery-image {
		cursor: pointer;
	}
	.gallery-zoom-icon {
		font-size: 20px;
		opacity: 0.5;
		position: absolute;
		right: 10px;
		top: 10px;
		z-index: 10;
	}
}

.product-details{
	margin-bottom: 10px;
	.name{
		.page-headline();
    padding: 0;

		.sku{
			font-size: 18px;
			line-height: 18px;
			color: @text-color-secondary;
			position: relative;
			padding-left: 20px;
			font-weight: 400;

			&:before {
				border-left: 1px solid @border-color-2;
				content: "";
				height: 14px;
				position: absolute;
				top: 7px;
				left: 10px;
			}
		}
		.code {
			font-size: 18px;
			line-height: 18px;
			padding-left: 5px;
		}
	}

	.promotion {
		color: @item-promo-color;
	}

	.description{
		padding: (@grid-gutter-width/2) 0 25px;
	}

	.price{
		font-size: 25px;
		font-weight: bold;
	}

	.stock-status{
		font-weight: 300;
	}
}

@media (max-width: @screen-md-max) {
	.product-details{
		.description{
			border-bottom: 1px solid @border-color-3;
		}
	}
}


.variant-section{
	margin-bottom: @grid-gutter-width + @grid-gutter-width/2;

	.variant-selector{
		.variant-name{
			font-size: 13px;
			font-weight: 600;
			color: @label-color;
			text-transform: uppercase;
			margin-top: 15px;
			margin-bottom: 5px;

			.variant-selected{
				font-weight: normal;
				text-transform: none;
			}
		}

		.variant-list{
			.list-unstyled;
			.clearfix;
			margin-left: -(@grid-gutter-width);

			li{
				float: left;
				margin: 0 0 0 (@grid-gutter-width);
				a,span{
					display: block;
					height: @input-height-base;
					width: @input-height-base;
					line-height: @input-height-base;
					text-align: center;
					text-transform: uppercase;
					border:1px solid @input-border-color;
					background: #fff;
					color: @text-color;
					text-decoration: none;

					@media (max-width: @screen-sm-max) {
						height: @input-height-large;
						width: @input-height-large;
						line-height: @input-height-large;
					}


					img{
						width: 100%;
						height: auto; 
						vertical-align: top;
						border:1px solid transparent;
					}
				}

				

				&:hover a,
				&.active a{
					background: @brand-primary;
					.box-shadow(~"inset 0 0  0 1px #fff");
					border:1px solid @brand-primary;
					color: #fff;
					

					img{
						border:1px solid #fff;
					}
				}


				&.disabled{
					span{
						color:#999;
						cursor: not-allowed;
					}
				}
			}
		}
		.variant-select{
			margin: (@grid-gutter-width/2) 0 0;
		}
	}
}


.rating{
	.clearfix;
	
	.rating-stars{
		float: left;
		margin: 0 5px 0 0;
		padding: 3px 0 1px;
		letter-spacing:50px;

		@media (max-width: @screen-sm-max) { 
			font-size: @font-size-large;
			float: none;
			.clearfix;
		}

		span{
			color: #dee3e8;
			overflow: hidden;
			position: static;
			width: 1em;
			height: 1em;
			display: block;
			float: left;
			margin: 0 0.1em;

	
			&.fh{
				width: 0.5em;	
				margin-right: 0;		
			}

			&.lh{
				width: 0.5em;
				text-indent: -0.5em;
				margin-left: 0;
			}

			&.active{
				color: @brand-primary;
			}
		}
	}

	&.rating-set{
		.rating-stars{
			cursor: pointer;
		}
	}


	a{
		font-size: 13px;
		position: relative;
		margin-left: 10px;
		padding-left: 10px;

		&:before {
			border-left: 1px solid @border-color-3;
			content: "";
			height: 19px;
			position: absolute;
			top: 0;
			left: 0;
		}
	}

}







.tabs-responsive{
	@media (max-width: @screen-sm-max) {
		.tabhead{
			border-bottom: 1px solid @grayblue;
			
		}

	}
}

.tab-review{
	.review-pagination-bar{
		margin: (@grid-gutter-width/2) 0;
		.clearfix;
		.right{
			float: right;
			.pager{
				float: left;
				margin: 0 (@grid-gutter-width/2);
			}

		}
	}


	.review-list{
		.list-unstyled;
		.review-entry{
			margin: (@grid-gutter-width/2) 0;

			.title{
				font-weight:bold;
			}

			.content{
				
			}

			.autor{
				
				color:@gray-light;

			}

			.date{
			
			}

		}
	}


	.write-review{
		display: none;
	}
}












