/*------- Hotel overview start -------*/
.panel-price-options {
	.panel-body{
		padding-top: 0;
		padding-bottom: 0;
	}
	.hotel-items {
		padding: 0;
		.price-option{
			background-color: #d7d7d7;
		    margin-bottom: 0;
		    border-left: none;
		    border-right: none;
		    border-bottom: none;
			> div{
				padding: 15px;
				&.hotel-details{
					background: #f1f2f2;
					height: 170px;
				}
			}
			li{
				border: none;
				margin-bottom: 0;
				background: transparent;
			}
		}
	}
}
/*------- Hotel overview end -------*/

/*------- Booking panels start -------*/

.booking {
	&.room-details {
		&.panel-list {
			.panel {
				&:first-child {
					border: 0;
				}
				.panel-heading {
					border: 0;
				}
			}
		}
	}
	&.panel.panel-list {
		.journey-primary-wrapper {
		    border-top: 1px solid #076480;
		    border-bottom: 1px solid #076480;
			margin: 10px 0;
			padding: 20px 0;
		}
		.journey-wrapper {
			.my-account-secondary-panel {
				margin-top: 0;
			    background: white;
			    border-left: 1px solid #4c9fb1;
			    border-right: 1px solid #4c9fb1;
			    border-bottom: 1px solid #4c9fb1;
			    .panel-heading {
					border-left: 0;
				    border-right: 0;
				}
			    .panel-body {
					border: 0;
				    border-top: 1px solid #4c9fb1;
				    margin-bottom: 0;
				}
			}
		}
		.my-account-secondary-panel {
			border-color: transparent;
			&.panel {
				box-shadow: none;
			}
			.room {
				border: 1px solid #4c9fb1;
				border-top: 0;
				.breakdown {
					padding-bottom: 20px;
				}
				.panel-body {
					padding-top: 0;
					margin-bottom: 0;
					border: 1px solid #4c9fb1;
					&.collapsing, &.collapse {
						border: 0;
					}
				}
				> .panel-body {
					border: 0;
					padding: 0;
				}
				.panel-heading {
					border: 0;
					border-top: 1px solid #4c9fb1;
					border-bottom: 1px solid #4c9fb1;
				}
			}
			.panel-heading {
				border: 1px solid #4c9fb1;
				border-bottom: 0;
				&.divided {
					border: 0;
				}
			}
			.panel-body {
				border: 1px solid #4c9fb1;
			}
			.review {
				margin-bottom: 20px;
				.panel-body {
					margin-bottom: 0;
				}
			}
		}
		.option-buttons button {
			margin-bottom: 20px;
		}
	}
	.review-score {
		@media (max-width: @screen-xs) {
			margin-bottom: 20px;
		    margin-top: 10px;
		    padding-left: 5px;
		    padding-right: 5px;
		}
		li {
			list-style-type: none;
			position: relative;
			&:last-child {
				label {
					border-right: 1px solid #ccc;
				}
		    }
		}
		label {
			padding: 5px;
		    text-align: center;
		    border: 1px solid #ccc;
		    border-right: 0;
		    width: 9%;
		    float: left;
		    margin-bottom: 0;
		    @media (max-width: @screen-xs) {
				padding: 5px 2px;
			}
		    &:hover {
		    	cursor: pointer;
		    	background-color: #08deff;
		    	color: #fff;
		    }
		}
		input[type="radio"] {
			opacity: 0;
			position: absolute;
			top: -20px;
			z-index: -1000;
			&:checked + label {
				background-color: #00b7d3;
				color: #fff;
			}
		}
	}
	.responsive-table-item {
		@media (max-width: @screen-xs) {
			padding: 0 15px;
		}
	}
}

.extras {
	margin-top: 20px;

}

#addRemoveModal {
	.modal-body {
		padding: 0;
	}
	.list-group {
		margin-bottom: 0;
	}
	.list-group-item {
		border: 0;
		border-bottom: 1px solid #ddd;
		margin-bottom: 0;
	}
}

.pre-amendment {
    position: relative;
    &:before {
        background: rgba(255,255,255, 0.5);
        position: absolute;
        top: 0;
        bottom: 0;
        left: 0;
        right: 0;
        display: block;
        z-index: 1;
    }
}

.booking-table {
	td {
		&.status {
			text-align: left;
		}
	}
}

/*------- Booking panels end -------*/