// Panels

.panel {
    border-radius: 0;
    &.panel-primary {
        background: #f1f2f2;
        .panel-heading {
            border-color: transparent; // Prevents double border when collapsed
            display: block;
        }
    }
    .list-group {
        margin-bottom: 0;
        &.radio-group {
            background-color: @brand-blue;
            padding: 0;
            input.passenger-seat {
                margin-top: 1.5em;
                cursor: pointer;
            }
            .list-group-item {
                padding: 0;
                border: none;
                transition: all .2s ease-in-out;
                &:hover,
                &:focus {
                    background: @secondary-blue;
                    position: static;
                }
                &.passenger-seat {
                    border-bottom: 1px solid #fff;
                    &:first-child {
                        border-top: 1px solid #fff;
                    }
                }
                .row {
                    margin: 0;
                }
                .passenger-group {
                    margin: 10px 0;
                    cursor: pointer;
                }
            }

        }
        &.no-padding {
            .list-group-item {
                padding-top: 0;
                padding-bottom: 0;
                border-top: 1px solid #83b2c0;
                &:last-child {
                    border-bottom: 1px solid #83b2c0;
                }
            }
        }
        .list-group-item {
        	padding:1px 15px;
            background-color: transparent;
            border: none;
            border-bottom: 1px solid #fff;
            &:first-child,
            &:last-child {
                border-radius: 0;
            }
        }
        .input-row {
            padding: 0;
            .link-trigger {
                margin: 0;
            }
            input.form-control {
                text-align: center;
            }
            select {
                color: @brand-blue;
            }
            option {
                padding: 5px;
            }
            @media (max-width: @screen-xs-max) {
                .input-stacked {
                    margin-top: 10px;
                }
            }
            .inline-fields-stacked {
                margin-bottom: 10px;
            }
        }
    }
    .panel-heading {
        border-radius: 0;
        background-color: @panel-header;
        position: relative;
        > *:first-child {
            width: 95%;
        }
        .title {
            margin-top: 0;
            margin-bottom: 0;
        }
    }
    .panel-body {}
    .tab-wrapper {
        margin-bottom: 0;
        padding-bottom: 0;
    }

    .iframe-panel {
        // float: none;
        background-color: #f1f2f2;
        padding-top: 18px;
    }
} // .panel

