// Styleguide 
.variable-colour {
    li {
        &:before {
            content: " ";
            position: absolute;
            width: 18px;
            height: 18px;
            background-color: red;
            margin-left: -40px;
            margin-top:2px;
            border: 1px solid #000;
        }
