    }
}

/*------- button default light end -------*/

/*------- Modify form start -------*/

.modify-search {
    border: none;
    .form-background {
        padding-bottom: 0;
        &.form-background-grey {
            background-color: #dcdddf;
        }
        .primary-legend {
            margin: 0;
            background-color: #bcbec0;
            padding-left: 50px;
            .panel-heading {
                background-color: transparent;
                color: #fff;
                &:focus {
                    text-decoration: none;
                }
            }
        }
    }
    .fieldset-inner-wrapper {
        padding-top: 20px;
    }
}

/*------- Modify form end -------*/

/*------- Average score bar start -------*/

.average-score {
    height: 26px;
    line-height: 26px;
    font-size: 16px;
    .score-value-wrapper,
    span {
        float: left;
        background: #eee;
    }
    span {
        width: 10%;
        text-align: center;
        font-weight: bold;
        &:first-child {
            border-top-left-radius: 5px;
            border-bottom-left-radius: 5px; 
        }
        &:last-child {
            border-top-right-radius: 5px;
            border-bottom-right-radius: 5px;
            width: 20%;
            span {
                color: #00b7d3;
                border-radius: 0;
                width: auto;
                float: none;
            }
        }
    }
    .score-value-wrapper {
        width: 70%;
        height: 100%;
        .score-value {
            background: #00b7d3;
            border-radius: 5px;
            height: 20px;
            margin-top: 3px;
            margin-bottom: 3px;
        }
    }
}

/*------- Average score bar end -------*/

/*------- Accommodation item ribbon/banner start -------*/

.ribbon-container {
    background: #ea1c4a;
    background-image: linear-gradient(rgba(255,255,255, 0.1), #89132e);
    position: absolute;
    left: 13px;
    top: -3px;
    z-index: 1;
    width: 60px;
    color: #fff;
    padding: 10px;
    text-transform: uppercase;
    text-align: center;
    font-weight: bold;
    display: block;
    &:before, &:after, span:before {
        content: "";
        display: block;
        position: absolute;
    }
    &:before, span:before {
        height: 0;
        width: 0;
        top: 60px;
    }
    &:before {
        left: 3px;
        border-top: 0px solid rgba(0, 0, 0, 0.3);
        border-bottom: 20px solid transparent;
        border-right: 30px solid rgba(0, 0, 0, 0.3);
        border-left: 30px solid rgba(0, 0, 0, 0.3);
    }
    &:after {
        width: 3px;
        background: rgba(0, 0, 0, 0.3);
        bottom: 0px;
        top: 3px;
        right: -3px;
    }
    span {
        &:before {
            left: 0;
            border-top: 0px solid #89132e;
            border-bottom: 20px solid transparent;
            border-right: 30px solid #89132e;
            border-left: 30px solid #89132e;
        }
    }
}

/*------- Accommodation item ribbon/banner end -------*/

/*------- Star icons start -------*/
.accommodation-star-rating {
    line-height: 24px;
    margin-bottom: 10px;
    color: #20b3ce;
    clear: both;
    @media (min-width: @screen-sm) {
        float: left;
    }
}
.person-icon{
    background: url("@{theme-images-url}/icon-person.png") no-repeat;
    width: 16px;
    height: 17px;
    display: inline-block;
}
.person-blue-icon{
    background: url("@{theme-images-url}/icon-person-blue.png") no-repeat;
    width: 16px;
    height: 17px;
    display: inline-block;
}
/*------- Star icons end -------*/

/*------- Rating start -------*/
