            margin-bottom: 0;
        }
    }
    .accommodation-image {
        position: absolute;
        left: 0;
        top: 0;
        bottom: 0;
        right: 0;
        z-index: 1;
        overflow: hidden;
        img {
            position: absolute;
            top: 50%;
            left: 50%;
            width: auto;
            height: auto;
            max-height: none;
            max-width: none;
            min-height: 100%;
            min-width: 100%;
            -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
            @media (min-width: @screen-sm) {
                margin-bottom: -15px;
                margin-left: -15px;
            }
        }
    }
    li,
    &.grid li > div {
        border: 1px solid #ccc;
        background: #f5f5f5;
    }
    .promoted,
    &.grid .promoted {
        border: 2px solid #f9a825;
        background: #fdebce;
    }
    li {
        padding: 0;
        @media (max-width: @screen-xs-max) {
            padding-top: 15px;
            padding-bottom: 15px;
        }
        margin-bottom: 20px;
    }
    .accommodation-details {
        position: relative;
        z-index: 1;
        @media (min-width: @screen-sm) {
            padding: 15px;
            small {
                font-size: 10px; 
                color: #076480;
                margin-bottom: 20px;
            }
        }
        h3 {
            @media (min-width: @screen-sm) {
                float: left;
                margin-right: 10px;
            }
            margin: 0;
        }
        p {
            @media (min-width: @screen-sm) {
                margin-top: 0;
            }
            span {
                line-height: 24px;
                font-size: 18px;
                display: block;
            }
        }
        .price {
            .previous-price {
                display: inline-block;
                position: relative;
                font-size: 18px;
                &:before {
                    content: "";
                    border: 1px solid #076480;
                    position: absolute;
                    right: -7px;
                    left: -7px;
                    transform: rotate(12deg);
                    bottom: 50%;
                }
            }
            .sale-price {
                color: #ea1c4a;
                font-size: 24px;
            }
            .room-types {
                list-style: none;
                padding: 0;
                li {
                    margin-bottom: 10px;
                    padding: 0;
                    background: transparent;
                    border: 0;
                    color: #076480;
                    font-size: 14px;
                }
            }
            p {
                margin-top: 0;
            }
            @media (min-width: @screen-xs) {
                text-align: right;
            }
        }
    }
    .price,
    .reviews {
        p {
            color: #076480;
            font-size: 14px;
            font-weight: 500;
            line-height: 1.1;
            margin-bottom: 0;
            span {
                display: block;
                font-size: 18px;
                line-height: 24px;
                margin-bottom: 0;
            }
        }
    }
    .price {
        p {
            font-size: 24px;
        }
    }
    /* fullwidth reorder with flex */
    @media (min-width: @screen-sm) {
        .flex-wrap {
            display: -moz-box;
            display: -webkit-box;
            display: -webkit-flex;
            display: -ms-flexbox;
            display: flex;
            -moz-box-orient: vertical;
            -webkit-box-orient: vertical;
            -webkit-flex-flow: column;
            -ms-flex-direction: column;
            flex-flow: column;
        }
        .price {
            -moz-box-ordinal-group: 1;
            -webkit-box-ordinal-group: 1;
            -webkit-order: 1;
            -ms-flex-order: 1;
            order: 1;
            @media (min-width: @screen-sm-min) {
                text-align: right;
            }
        }
        .reviews {
            -moz-box-ordinal-group: 2;
            -webkit-box-ordinal-group: 2;
            -webkit-order: 2;
            -ms-flex-order: 2;
            order: 2;
        }
    }

    /*------- Accommodation result list grid start -------*/
    &.grid {
        li {
            padding: 0 15px;
            border: 0;
            background: transparent;
            & > div {
                padding-top: 15px;
            }
            .accommodation-image {
                left: 16px;
                right: 16px;
                top: 1px;
                height: 150px;
            }
            .promoted {
                .accommodation-image {
                    left: 17px;
                    right: 17px;
                    top: 2px;
                    height: 149px;
                }
            }
            .accommodation-details {
                padding-top: 150px;
            }
            &:nth-child(3n+1) {
                clear: left;
            }
        }
        .ribbon-container {
            left: 28px;
        }
    }
    /*------- Accommodation result list grid end -------*/
}
