/*------- Accommodation Image Gallery component start -------*/
.accommodation-image-gallery {
    .owl-carousel .owl-controls {
        display: block !important;
        .owl-buttons {
            .owl-prev, .owl-next {
                font-size: 30px;
                height: auto;
                width: 30px;
            }
            .owl-prev {
                &:before{content:"\e079";}
            }
            .owl-next {
                &:before{content:"\e080";}
            }
        }
    }

    .main-image-carousel .item {
        background: #0c83e7;
        color: #FFF;
        -webkit-border-radius: 3px;
        -moz-border-radius: 3px;
        border-radius: 3px;
        text-align: center;
        img {
            width: 100%;
        }
    }
    .thumbnail-carousel-wrapper {
        margin: 0px 30px;
    }
    .thumbnail-carousel {
        .item {
            background: #C9C9C9;
            margin: 4px 2px;
            color: #FFF;
            -webkit-border-radius: 3px;
            -moz-border-radius: 3px;
            border-radius: 3px;
            text-align: center;
            cursor: pointer;
            opacity: 0.8;
            img {
                width: 100%;
            }
        }
        .owl-controls .owl-buttons {
            .owl-prev, .owl-next {
                color: #bcbec0;
            }
            .owl-prev{
                left: -30px;
            }
            .owl-next{
                right: -30px;
            }
        }
    }
    .thumbnail-carousel .synced .item {
        border: 1px solid #20b3ce;
        opacity: 1;
    }
}

