/*------- Accommodation tab start -------*/
.tab-wrapper {
    position: relative;
    &.full-tabs {
        border: none;
        padding-bottom: 0;
        .nav.nav-tabs {
            width: 100%;
            display: block;
            background-color: transparent;
            padding-top: 0px;
            border-bottom: none;
            > li {
                list-style: none;
                float: left;
                text-align: center;
                border-left: 1px solid #ccc;
                border-right: 1px solid #ccc;
                width: 33.33333%; /* fallback for non-calc() browsers */
                width: calc(100% / 3);
                box-sizing: border-box;
                a{
                    background: #f1f2f2;
                    border-top: 1px solid #c0c1c3;
                    margin-right: 0;
                    border-bottom: 1px solid #ccc;
                }
                &.active a {
                    background-color: #d7d7d7;
                    color: #414042;
                    padding-top: 10px;
                    margin: 0;
                    border-bottom: 1px solid transparent;
                }
            }
        }
        .tab-content {
            color: #414042;
            border-left: 1px solid #ccc;
            border-right: 1px solid #ccc;
            border-bottom: 1px solid #ccc;
            > .tab-pane{
                &.active{
                    background-color: #d7d7d7;
                } 
                > .row{
                    padding: 15px;
                }
                .content-right{
                    padding-left: 0;
                }
