        font-weight: bold;
        margin-bottom: 0;
        span {
            position: absolute;
            bottom: -15px;
            right: 0;
            @media (max-width: @screen-sm-max) {
                right: auto;
                left: 0;
                bottom: -30px;
            }
        }
    }
    .moon {
        width: 25px;
        height: 25px;
        border-radius: 50%;
        box-shadow: 7px 7px 0 0 #fff;
        position: absolute;
        top: -5px;
        transform: rotate(-26deg);
    }
}

.landing-form-background { 
    position: relative;
    overflow-x: hidden;
    margin-bottom: 20px;
    @media (min-width: @screen-sm-min) {
        min-height: 760px;
            &:before {
            height: 300px;
            content: "";
            position: absolute;
            bottom: 0;
            left: 0;
            right: 0;
            background-image:
            linear-gradient(
                rgba(0,0,0,0), 70%, #fff
            );
        }
    }
    @media (max-width: @screen-sm-max) {
        background-image: none !important;
    }
}

.guest-types {
    margin-top: 10px;
    & > .row {
        border-bottom: 1px solid #fff;
        padding-bottom: 20px;
    }
}

/*------- Accommodation search form end -------*/

.room-wrap {
    padding-top: 15px;
}

.facets-search-container {
    ul {
        list-style-type: none;
        padding: 15px;
        border: 2px solid #fff;
        position: relative;
        @media (min-width: @screen-sm-min) and (max-width: @screen-sm-max) {
            &:nth-child(4n+1) {
                clear: left;
            }
        }
        @media (min-width: @screen-md-min) {
            &:nth-child(6n+1) {
                clear: left;
            }
        }
        @media (max-width: @screen-xs-max) {
            &:nth-child(2n+1) {
                clear: left;
            }
            background-color: #ededee;
            margin-bottom: 0;
            border: 1px solid #f5f5f5;
        }
        &:before {
            content: "";
            position: absolute;
            z-index: 0;
            top: 0;
            left: 0;
            right: 0;
            bottom: 0;
            border: 1px solid #ddd;
        }
    }
    .btn-close-facet {
        position: absolute;
        right: 5px;
        top: 5px;
        .glyphicon-remove {
            background: #fff;
            border: 1px solid #ddd;
        }
    }
    .glyphicon-remove {
        color: #666;
        font-size: 12px;
        padding: 1px;
    }

    .search-category {
        font-weight: bold;
        margin: 10px 0 0;
    }

    .facet-item {
        font-size: 12px;
        padding: 2px 0 0 0;
    }
}

/* Google Location Map */
.accommodation-map {
    padding-top: 5px;

    .accommodation-star-rating {
        margin-bottom: 0;
    }
    
    li {
        margin-bottom: 0;
    }

    .ribbon-container span:before {
        top: 55px;
    }
}

/* Google Location Map end */