input[type="radio"],
input[type="checkbox"]{
  margin-right: 10px;
  margin-bottom: 20px;
}

label {
	color: @label-color;
	font-weight: 600;
	text-transform: uppercase;
	font-size: 13px;
}

.form-control:focus {
	border-color: @input-border-focus;
	outline:0;
	box-shadow: none;
}


.form-control::-moz-placeholder {
	color: @placeholder-color;
	opacity:1
}
.form-control:-ms-input-placeholder,
.form-control::-webkit-input-placeholder {
	color: @placeholder-color;
}

select.form-control {
	text-transform: uppercase;
	font-size: 13px;
}

.form-control {
  &[disabled],
  &[readonly],
  fieldset[disabled] & {
    color: @text-color-secondary;
  }
}
.has-error {
  input.form-control {
    background: @input-error-bg;
    border-color: @input-error;
  }

  label.control-label {
    color: @input-error;
  }

  .help-block {
    color: @input-error;

  }
}
legend {
  font-size: 18px;
  border-bottom: 0;
  margin-top: @grid-gutter-width;
  margin-bottom: @grid-gutter-width;
}
// For input fields with glyphicon
.form-element-icon {
  position: relative;

  .glyphicon {
    position: absolute;
  }

  &.datepicker {
    .glyphicon {
      top: 32px;
      right: 10px;
      color: @brand-primary;
      cursor: pointer;

      @media (max-width: @screen-sm-max) {
        top: 38px;
      }
    }
  }
}

// radio button styling
.form-control-radio,
.form-control-checkbox {
  display: block;

  input{
    margin-bottom: 0;
    float: left;
  }
  .form-control-label {
    margin-left: 25px;
  }
}

fieldset {
  > div:last-of-type {
    input,
    label {
      margin-bottom: 0;
    }
  }
}